import React, {Suspense, useEffect, useState, useContext} from "react"
import { Popups } from "./popups";
import { BlocksContext } from "../context/blocksContext"
// import { Main_screen } from './main_screen';
import { Menu } from './menu';
import { Header } from './header';
import { About_project } from './about_project';
import { Presentation } from './presentation';
import { Galery } from './galery';
import { Loc } from './loc';
import { Genplan } from './genplan';
import { Infra } from './infra';
import { Advantage } from './advantage';
import { Repair_apart } from './repair_apart';
import { Plan_price } from './plan_price';
import { Vacant_apart } from './vacant_apart';
import { How_buy } from './how_buy';
import { Parking } from './parking';
import { Kellers } from './kellers';
import { About_us } from './about_us';
import { Hod_str } from './hod_str';
import { Contacts } from './contacts';
import { Footer } from './footer';



export const Loader = () => {
    //const [loaded, setLoaded] = useState(2)

    const blocksImports = {
        // 'main_screen': <Main_screen />,
        'menu': <Menu />,
        'header': <Header />,
        'about_project': <About_project />,
        'presentation': <Presentation />,
        'galery': <Galery />,
        'loc': <Loc />,
        'genplan': <Genplan />,
        'infra': <Infra />,
        'advantage': <Advantage />,
        'repair_apart': <Repair_apart />,
        'plan_price': <Plan_price />,
        'vacant_apart': <Vacant_apart />,
        'how_buy': <How_buy />,
        'parking': <Parking />,
        'kellers': <Kellers />,
        'about_us': <About_us />,
        'hod_str': <Hod_str />,
        'contacts': <Contacts />,
        'footer': <Footer />,
    }

    
    const blocks = ["header", "menu", "about_project", "presentation", "galery", "loc", "genplan", "infra", "advantage", "repair_apart", "plan_price", "vacant_apart", "how_buy", "parking", "kellers", "about_us", "hod_str", "contacts", "footer"]

    const loaded = useContext(BlocksContext)

    const LoadBlock = (block) => {
        return  <Suspense  fallback={<div>Загрузка...</div>}>{blocksImports[block]}</Suspense>
    }

    const generateHtml = () =>{
        let toDraw = []
        for(let i=0;i<loaded.blocks; i++){
            toDraw.push(LoadBlock(blocks[i]))
        }
        return (
            <div className="blocks" data={loaded.menuClick?"true":""}>
                <Popups />
                {toDraw.map((block) => block)}
            </div>
        )
    }
    const  handleScroll = (event) => {

        if (loaded.blocks< blocks.length){
           
            loaded.setBlocks(20)
            loaded.setPopup(true)
           
        } else {
           window.removeEventListener('scroll', handleScroll, true);
        }
    }
    useEffect(()=> {
        if (loaded.blocks<blocks.length){
            window.addEventListener('scroll', handleScroll, true);
        }
    })
    return generateHtml()
    
}