
import {React, useState} from "react"
import TextField from '@material-ui/core/TextField'
import InputMask from 'react-input-mask'
import Slider from '@material-ui/core/Slider';
import {useSendForm} from '../hooks/send-from.hook'

export const Vacant_apart = () => {
    const sendForm = useSendForm();
	const [floor, setFloor] = useState([4, 10])
    const [rooms, setRooms] = useState("все")

    const roomsClick = (event) => {
        event.preventDefault()
        setRooms(event.target.getAttribute('data'))
    }

    const handleChange = (event, newValue) => {
        setFloor(newValue);
      };

    return (
        <section className="vacant_apart">
            <img src="/images/dec10.svg" className="dec10" />
            <div className="cw_main">
                <div className="tm">
                    <span>Получите подборку <br/> свободных квартир</span>
                    <img src="/images/dec1.svg" className="dec1" />
                    <img src="/images/dec2.svg" className="dec2" />
                    <img src="/images/dec5.svg" className="dec5" />
                </div>
                <p className="vac_info">Перезвоним и предоставим <br/> выгодные предложения на сегодня</p>
                <div className="va_param">
                    <div className="va_nav">
                        <a href="" onClick={roomsClick} data="все" className={rooms=="все"?"act":""}>все</a>
                        <a href="" onClick={roomsClick} data="1-комнатные" className={rooms=="1-комнатные"?"act":""}>1-ком</a>
                        <a href="" onClick={roomsClick} data="2-комнатные" className={rooms=="2-комнатные"?"act":""}>2-ком</a>
                        <a href="" onClick={roomsClick} data="3-комнатные" className={rooms=="3-комнатные"?"act":""}>3-ком</a>
                        <a href="" onClick={roomsClick} data="4-комнатные" className={rooms=="4-комнатные"?"act":""}>4-ком</a>
                    </div>
                    <div className="va_floor_slid">
                        <span className="va_txt">Выберите этаж</span>
                        <Slider className="slid_style_react"
                                //defaultValue={floor}
                                value={floor}
                                step={1}
                                min={2}
                                max={14}
                                onChange={handleChange}
                                valueLabelDisplay="on"
                            />
                    </div>
                </div>
                <form className="form_main">
                    <div className="form_in react_input_style">
                        <TextField name="name" label="Ваше имя"  className=""/>
                    </div>
                    <div className="form_in react_input_style">
                        <InputMask mask="+7\ (999) 999-99-99" name="phone" maskChar={null}>
                            <TextField name="phone" label="Ваш телефон"  className=""/>
                        </InputMask>
                    </div>
                    <input type="hidden" className="dop-info slider-floor" data="Этаж" value={`c ${floor[0]} по ${floor[1]} этажи`} />
					<input type="hidden" className="dop-info rooms" data="Кол-во комнат" value={rooms} />
					<input type="hidden" className="text" value="Получить подборку" />
                    <button className="btn_main" celtype="getPodbor" onClick={sendForm.sendForm}>Записаться на просмотр​</button>
                </form>
            </div>
		</section>
    )
}