import React from 'react'

export const How_buy = () => {
    const showPopup = (e)=>{
        e.preventDefault()
        document.querySelector('body').classList.add('overflow')
        document.querySelector('.popup_rgba').style.display="block"
        document.querySelectorAll('.popup_main').forEach(el => {
            el.style.display="none"
        });
        console.log(e.currentTarget.getAttribute('data'))
        document.querySelector('.'+e.currentTarget.getAttribute('data')).style.display="block"
    }


    return (
        <section className="how_buy">
            <div className="cw_main">
                <div className="tm">
                    <span>Способы покупки<br/> квартиры</span>
                    <img src="/images/dec8.svg" className="dec8" />
                    <img src="/images/dec5.svg" className="dec5" />
                    <img src="/images/dec10.svg" className="dec10" />
                </div>
                <p class="hb_info">При покупке предоставляется<br/> бесплатное юридическое оформление ​</p>
                <div className="hb_list">
                    <div className="hb_el hb_ipot" onClick={showPopup} data="pu_ipot">
                        <img src="images/ipot_ico.png" />
                        <span>Ипотека</span>
                        <a href="#">Подробнее</a>
                    </div>
                    <div className="hb_el hb_rassr"  onClick={showPopup} data="pu_rassr">
                        <img src="images/rassr_ico.png" />
                        <span>Рассрочка</span>
                        <a href="#">Подробнее</a>
                    </div>
                    <div className="hb_el hb_mat_kap" onClick={showPopup} data="pu_mat_kap">
                        <img src="images/mat_kap_ico.png" />
                        <span>Материнский <br/> капитал</span>
                        <a href="#">Подробнее</a>
                    </div>
                    <div className="hb_el hb_distance" onClick={showPopup} data="pu_distance">
                        <img src="images/distance_ico.png" />
                        <span>Дистанционная<br/> сделка</span>
                        <a href="#">Подробнее</a>
                    </div>
                </div>
                <img src="images/hb_img.png" className="hb_img"/>
            </div>
		</section>
    )
}