import React from 'react'

export const Kellers = () => {
    const showPopup = (e)=>{
        e.preventDefault()
        document.querySelector('body').classList.add('overflow')
        document.querySelector('.popup_rgba').style.display="block"
        document.querySelectorAll('.popup_main').forEach(el => {
            el.style.display="none"
        });
        console.log(e.currentTarget.getAttribute('data'))
        document.querySelector('.'+e.currentTarget.getAttribute('data')).style.display="block"
    }


    return (
        <section className="kellers">
            <div className="cw_main">
                <div className="tm">
                    <span>Келлеры</span>
                    <img src="/images/dec2.svg" className="dec2" />
                    <img src="/images/dec3.svg" className="dec3" />
                </div>
                <img src="/images/kellers_img.png" className="kellers_img" />
                <p class="kellers_text">
                    <span> Келлерная система хранения сегодня очень популярна в Европе, и жители “Каштанового двора” также смогут оценить ее удобство. </span>
                    <span>Келлеры – отдельные помещения для хранения вещей: санок, лыж, мангалов, чемоданов и т.д. Они позволяют освободить место в ваших квартирах.</span>
                </p>
                <a href="#" onClick={showPopup} data="pu_keller">Выбрать келлер</a>
            </div>
		</section>
    )
}