import React from 'react'

export const Parking = () => {
    const showPopup = (e)=>{
        e.preventDefault()
        document.querySelector('body').classList.add('overflow')
        document.querySelector('.popup_rgba').style.display="block"
        document.querySelectorAll('.popup_main').forEach(el => {
            el.style.display="none"
        });
        console.log(e.currentTarget.getAttribute('data'))
        document.querySelector('.'+e.currentTarget.getAttribute('data')).style.display="block"
    }

    return (
        <React.Fragment>
        <section className="parking">
            <div className="cw_main">
                <div className="prk_wrap">
                    <div className="tm">
                        <span>Подземный <br/> паркинг</span>
                        <img src="/images/dec1.svg" className="dec1" />
                        <img src="/images/dec2.svg" className="dec2" />
                    </div>
                    <p class="park_info">Для автолюбителей в ЖК “Каштановый двор” <br/> оборудован теплый подземный паркинг.</p>
                    <div className="prk_list">
                        <span>371 машиноместо (габариты – 2,5 м * 5,3 м)</span>
                        <span>2 уровня</span>
                        <span>Детально выверенные радиусы поворотов и <br/> размерыпроездов для удобства въезда и выезда</span>
                    </div>
                    <a href="#" data={"pu_parking"} onClick={showPopup}>Выбрать парковочное место</a>
                </div>
                <img src="/images/parking_img.jpg" className="parking_img" />
            </div>
		</section>
        </React.Fragment>
    )
}