import  { React,  useRef, useState } from "react"
import Slider from "react-slick"
import "./css/slick.min.css"
import "./css/slick-theme.min.css"

export const Galery = () => {
    const slider = useRef(null);
    const [flag, setFlag] = useState(false)

    var settings = {
        dots: false,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
    };

    const clickType = (flag) => {
        setFlag(flag);
    }
    
    return (
            <section className="galery">
                <img src="/images/dec2.svg" className="dec2" />
                <img src="/images/dec3.svg" className="dec3" />
                <div className="cw_main">
                    <div className="tm">
                        <span>фотогалерея</span>
                        <img src="/images/dec3.svg" className="dec3" />
                        <img src="/images/dec6.svg" className="dec6" />
                    </div>          
                </div>
                <Slider ref={slider}  className="gl_slid" {...settings}>
                    {Array.from(Array(10).keys()).map((index) => {
                        if (flag) {
                            return  <div className="gl_in"><img src={"/images/gallery/entrance/entrance_"+(index+1)+".jpg"}/></div>
                        } else {
                            return  <div className="gl_in"><img src={"/images/gallery/yard/yard_"+(index+1)+".jpg"}/></div>
                        }
                    })}
                </Slider>
                <div className="cw_main gl_nav_wrap">
                    <div className="gl_nav">
                        <a href="#" className={!flag?"act":""} onClick={(e)=>{e.preventDefault(); setFlag(false)}}>Двор</a>
                        <a href="#" className={flag?"act":""} onClick={(e)=>{e.preventDefault(); setFlag(true)}}>Подъезды</a>
                    </div>
                    <div class="gl_nav_l" onClick={()=>{slider.current.slickPrev()}}><span></span></div>
                    <div class="gl_nav_r" onClick={()=>{slider.current.slickNext()}}><span></span></div>
                </div>
            </section>
    )
}